.widget-sm {
  flex: 1;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75); 
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75); 
  margin-right: 20px;
}

.widget-sm-title {
  font-size: 22px;
  font-weight: 600;
  margin-left: 10px;
}

.widget-sm-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.widget-sm-item {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin: 20px 0px;
}

.widget-sm-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.widget-sm-user {
  display: flex;
  flex-direction: column;
}

.widget-sm-username {
  font-weight: 600;
}

.widget-sm-user-title {
  font-weight: 300;
}

.widget-sm-button {
  display: flex;
  align-items: center;
  border: none;
  border-radius: 10px;
  padding: 7px 10px;
  background-color: #eeeef7;
  color: #555;
  cursor: pointer;
}

.widget-sm-icon {
  font-size: 16px !important;
  margin-right: 5px;
}