.product {
  flex: 4;
  padding: 20px;
}

.product-title-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.product-add-button {
  width: 80px;
  border: none;
  padding: 5px;
  background-color: teal;
  color: white;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
}

.product-top {
  display: flex;
}

.product-top-left, .product-top-right {
  flex: 1;
}

.product-top-right {
  padding: 20px;
  margin: 20px;
  -webkit-box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75); 
  box-shadow: 0px 0px 15px -10px rgba(0, 0, 0, 0.75);
}

.product-infor-img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 20px;
}

.product-infor-top {
  display: flex;
  align-items: center;
}

.product-name {
  font-weight: 600;
}

.product-infor-bottom {
  margin-top: 10px;
}

.product-infor-item {
  width: 150px;
  display: flex;
  justify-content: space-between;
}

.product-infor-value {
  font-weight: 300;
}

.product-form {
  display: flex;
  justify-content: space-between;
}

.product-form-left {
  display: flex;
  flex-direction: column;
}

.product-form-left>label {
  margin-bottom: 10px;
  color: gray;
}

.product-form-left>input {
  margin-bottom: 10px;
  border: none;
  padding: 5px;
  border-bottom: 1px solid gray;
}

.product-form-left>select {
  margin-bottom: 10px;
}

.product-upload-img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
  object-fit: cover;
  margin-right: 20px;
}

.product-form-right {
  display: flex;
  flex-direction: column;
  justify-content: space-;
}

.product-upload {
  display: flex;
  align-items: center;
}

.product-button {
  text-align: center  ;
  border: none;
  padding: 5px;
  border-radius: 5px;
  background-color: rgb(76, 76, 236);
  color: white;
  font-weight: 600;
}